import { Component } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ConfigurationSnapshot } from './device-configuration.model';
import { AlertService, gettext } from '@c8y/ngx-components';
import { DeviceConfigurationService } from './device-configuration.service';

@Component({
  selector: 'c8y-save-config-to-configuration-repository',
  templateUrl: 'save-to-repository.component.html'
})
export class SaveToRepositoryComponent {
  configSnapshot: ConfigurationSnapshot;
  result: Promise<any> = new Promise((resolve, reject) => {
    this._save = resolve;
    this._cancel = reject;
  });
  private _save;
  private _cancel;

  constructor(
    private modal: BsModalRef,
    private alertService: AlertService,
    private deviceConfigurationService: DeviceConfigurationService
  ) {}

  async save() {
    try {
      await this.deviceConfigurationService.saveToConfigurationRepository(this.configSnapshot);
      this.alertService.success(gettext('Configuration saved.'));
      this._save();
    } catch (ex) {
      if (ex.data) {
        this.alertService.danger(ex.data.message);
      }
    }
  }

  close() {
    this._cancel();
    this.modal.hide();
  }
}
