import { EventEmitter, Injectable } from '@angular/core';
import {
  EventBinaryService,
  EventService,
  IEvent,
  IManagedObject,
  InventoryBinaryService,
  InventoryService,
  IOperation,
  OperationService
} from '@c8y/client';
import { AlertService, gettext } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';

@Injectable()
export class DeviceConfigurationService {
  readonly dateFrom = new Date(0);
  readonly dateTo = new Date(Date.now() + 86400000); // 1 day in the future
  repositoryConfigListUpdated = new EventEmitter<any>();

  constructor(
    private eventService: EventService,
    private eventBinaryService: EventBinaryService,
    private operationService: OperationService,
    private alertService: AlertService,
    private inventoryService: InventoryService,
    private inventoryBinaryService: InventoryBinaryService,
    private translateService: TranslateService
  ) {}

  async getLatestConfigurationEvent(deviceId: string | number, type: string): Promise<IEvent> {
    const filter: object = {
      source: deviceId,
      type,
      dateFrom: this.dateFrom.toISOString(),
      dateTo: this.dateTo.toISOString(),
      pageSize: 1
    };

    const { data } = await this.eventService.list(filter);
    return data[0];
  }

  async getEventConfigurationBinary(event: IEvent): Promise<string> {
    let binary;
    try {
      const res = await this.eventBinaryService.download(event);
      binary = res.text();
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
    return binary;
  }

  async createUploadConfigFileOperation(device, configurationType) {
    let operation;
    const operationText = gettext(
      'Retrieve {{ configurationType }} configuration snapshot from device {{ deviceName }}'
    );
    const operationCfg: IOperation = {
      deviceId: device.id,
      c8y_UploadConfigFile: {
        type: configurationType
      },
      description: this.translateService.instant(operationText, {
        configurationType,
        deviceName: device.name
      })
    };

    try {
      const { data } = await this.operationService.create(operationCfg);
      operation = data;
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
    return operation;
  }

  async getLatestConfigFileOperation(deviceId, configType, operationType): Promise<IOperation> {
    const filter: object = {
      deviceId,
      fragmentType: operationType,
      dateFrom: this.dateFrom.toISOString(),
      dateTo: this.dateTo.toISOString(),
      revert: true,
      pageSize: 2000
    };

    const { data } = await this.operationService.list(filter);
    return data.find(op => op[operationType].type === configType);
  }

  async getSnapshotsFromRepository(deviceType, configurationType): Promise<IManagedObject[]> {
    let query: any;
    const filter: object = {
      pageSize: 2000,
      withTotalPages: true
    };
    query = {
      __filter: {
        __and: [
          {
            type: 'c8y_ConfigurationDump'
          },
          {
            __or: [
              { configurationType: { __eq: configurationType } },
              {
                __not: {
                  __has: 'configurationType'
                }
              }
            ]
          }
        ]
      },
      __orderby: [{ configurationType: -1, name: 1 }]
    };

    if (deviceType) {
      const deviceTypeFilter = {
        __or: [
          { deviceType: { __eq: deviceType } },
          {
            __not: {
              __has: 'deviceType'
            }
          }
        ]
      };
      query.__filter.__and.push(deviceTypeFilter);
    }
    return (await this.inventoryService.listQuery(query, filter)).data;
  }

  async createDownloadConfigFileOperation(device, configurationType, binaryUrl) {
    let operation;
    const operationText = gettext(
      'Send configuration {{ configurationType }} to device {{ deviceName }}'
    );
    const operationCfg: IOperation = {
      deviceId: device.id,
      c8y_DownloadConfigFile: {
        url: binaryUrl,
        type: configurationType
      },
      description: this.translateService.instant(operationText, {
        configurationType,
        deviceName: device.name
      })
    };
    try {
      const { data } = await this.operationService.create(operationCfg);
      operation = data;
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
    return operation;
  }

  async getConfigurationBinaryFile(url) {
    let binary;
    const moId = this.inventoryBinaryService.getIdFromUrl(url);
    if (moId) {
      const binaryMO = (await this.inventoryService.detail(moId)).data;
      const res = await this.inventoryBinaryService.download(binaryMO);
      binary = await res.text();
    } else {
      binary = this.fetchExternalBinary(url);
    }
    return binary;
  }

  async saveToConfigurationRepository(config) {
    const mo: Partial<IManagedObject> = {};
    Object.assign(mo, {
      name: config.name,
      type: 'c8y_ConfigurationDump',
      deviceType: config.deviceType,
      configurationType: config.configurationType,
      c8y_Global: {}
    });
    const response = await this.inventoryBinaryService.create(new Blob([config.binary]), {
      c8y_Global: {}
    });
    mo.url = response.data.self;
    return this.inventoryService.create(mo);
  }

  updateRepositoryConfigList() {
    this.repositoryConfigListUpdated.emit();
  }

  private async fetchExternalBinary(url) {
    let configBinary;
    try {
      const res = await fetch(url);
      if (res.status === 200) {
        configBinary = await res.text();
      }
    } catch (ex) {
      // do nothing
    }
    return configBinary;
  }
}
