import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ConfigurationSnapshot, DeviceConfigurationOperation } from './device-configuration.model';
import { IManagedObject, IOperation, OperationStatus, Realtime, UserService } from '@c8y/client';
import { DeviceConfigurationService } from './device-configuration.service';
import { Subscription } from 'rxjs';
import { saveAs } from 'file-saver/FileSaver';
import { BsModalService } from 'ngx-bootstrap/modal';
import { SaveToRepositoryComponent } from './save-to-repository.component';
import { cloneDeep } from 'lodash-es';
import { AppStateService } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-device-configuration-preview',
  templateUrl: './configuration-preview.component.html'
})
export class ConfigurationPreviewComponent implements OnInit, OnDestroy {
  @Input() device: IManagedObject;
  @Input() configurationType: string;
  @Input() configSnapshot: ConfigurationSnapshot;
  @Input() canSaveSnapshot: boolean;
  @Input() actionButtonText: string;
  @Input() actionButtonIcon: string;
  @Input() operationToTrigger:
    | DeviceConfigurationOperation.UPLOAD_CONFIG
    | DeviceConfigurationOperation.DOWNLOAD_CONFIG;

  operation: IOperation;
  private operationsSubscription: Subscription;

  constructor(
    private deviceConfigurationService: DeviceConfigurationService,
    private realtime: Realtime,
    private bsModal: BsModalService,
    private user: UserService,
    private appState: AppStateService
  ) {}

  async ngOnInit() {
    if (this.operationToTrigger === DeviceConfigurationOperation.UPLOAD_CONFIG) {
      const configOperation = await this.getOperation();
      this.updateOperation(configOperation);
    }

    const operationsChannel = `/operations/${this.device.id}`;
    this.operationsSubscription = this.realtime
      .observable(operationsChannel)
      .subscribe(({ data }) => {
        this.updateOperation(data);
      });
  }

  async createDeviceOperation() {
    if (this.operationToTrigger === DeviceConfigurationOperation.DOWNLOAD_CONFIG) {
      this.operation = await this.deviceConfigurationService.createDownloadConfigFileOperation(
        this.device,
        this.configurationType,
        this.configSnapshot.binaryUrl
      );
    }
    if (this.operationToTrigger === DeviceConfigurationOperation.UPLOAD_CONFIG) {
      this.operation = await this.deviceConfigurationService.createUploadConfigFileOperation(
        this.device,
        this.configurationType
      );
    }
  }

  showOperation(): boolean {
    if (this.operationToTrigger === DeviceConfigurationOperation.DOWNLOAD_CONFIG) {
      return !!this.operation;
    }
    return (
      this.operation &&
      [OperationStatus.PENDING, OperationStatus.EXECUTING, OperationStatus.FAILED].includes(
        this.operation.status
      )
    );
  }

  showBinary(): boolean {
    if (this.operationToTrigger === DeviceConfigurationOperation.DOWNLOAD_CONFIG) {
      return true;
    }
    return !this.showOperation();
  }

  isCreateOperationDisabled(): boolean {
    return (
      this.operation &&
      [OperationStatus.PENDING, OperationStatus.EXECUTING].includes(this.operation.status)
    );
  }

  updateOperation(operation: IOperation) {
    if (
      operation &&
      operation[this.operationToTrigger] &&
      operation[this.operationToTrigger].type &&
      operation[this.operationToTrigger].type === this.configurationType
    ) {
      this.operation = operation;
    }
  }

  getOperation() {
    return this.deviceConfigurationService.getLatestConfigFileOperation(
      this.device.id,
      this.configurationType,
      this.operationToTrigger
    );
  }

  download() {
    const blob = new Blob([this.configSnapshot.binary]);
    const fileName = this.configSnapshot.name;
    saveAs(blob, fileName);
  }

  async saveToRepository() {
    const initialState = {
      configSnapshot: cloneDeep(this.configSnapshot)
    };
    const modal = this.bsModal.show(SaveToRepositoryComponent, {
      class: 'modal-sm',
      initialState,
      ignoreBackdropClick: true
    }).content as SaveToRepositoryComponent;
    try {
      await modal.result;
      this.deviceConfigurationService.updateRepositoryConfigList();
      modal.close();
    } catch (ex) {
      // do nothing
    }
  }

  hasPermission(): boolean {
    return this.user.hasAnyRole(this.appState.currentUser.value, [
      'ROLE_INVENTORY_ADMIN',
      'ROLE_INVENTORY_CREATE'
    ]);
  }

  ngOnDestroy() {
    this.operationsSubscription.unsubscribe();
  }
}
