import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IEvent, IManagedObject, Realtime } from '@c8y/client';
import { DeviceConfigurationService } from './device-configuration.service';
import {
  ConfigurationListEmptyState,
  ConfigurationSnapshot,
  SupportedConfigurationItem
} from './device-configuration.model';
import { Subscription } from 'rxjs';
import { gettext } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-device-configuration',
  templateUrl: './device-configuration.component.html'
})
export class DeviceConfigurationComponent implements OnInit, OnDestroy {
  supportedConfigurations: SupportedConfigurationItem[];
  configurationType: string;
  configSnapshot: Partial<ConfigurationSnapshot> = {};
  repositorySnapshots: IManagedObject[];
  repositorySnapshot: ConfigurationSnapshot;
  repositorySnapshotsEmptyState: ConfigurationListEmptyState;
  device: IManagedObject;

  private eventsSubscription: Subscription;

  constructor(
    private route: ActivatedRoute,
    private deviceConfigurationService: DeviceConfigurationService,
    private realtime: Realtime
  ) {
    this.deviceConfigurationService.repositoryConfigListUpdated.subscribe(async () => {
      this.repositorySnapshot = undefined;
      this.repositorySnapshots = await this.getSnapshotsFromRepository(
        this.device.type,
        this.configurationType
      );
    });
  }

  ngOnInit() {
    this.device = this.route.snapshot.parent.data.contextData;
    this.supportedConfigurations = this.device.c8y_SupportedConfigurations.map(item => ({
      name: item
    }));

    this.repositorySnapshotsEmptyState = {
      icon: 'gears',
      title: gettext('No configurations available.'),
      text: gettext('Add configuration to configuration repository')
    };
    const eventsChannel = '/eventsWithChildren/' + this.device.id;
    this.eventsSubscription = this.realtime.observable(eventsChannel).subscribe(({ data }) => {
      this.updateConfigSnapshotOnEvent(data);
    });
  }

  async onConfigTypeSelected(config) {
    this.configurationType = undefined;
    this.repositorySnapshot = undefined;
    this.configSnapshot = {};

    const configEvent = await this.deviceConfigurationService.getLatestConfigurationEvent(
      this.device.id,
      config.name
    );
    this.updateConfigSnapshotOnEvent(configEvent, config.name);

    this.configurationType = config.name;
    this.repositorySnapshots = await this.getSnapshotsFromRepository(this.device.type, config.name);
  }

  getSnapshotsFromRepository(deviceType, configurationType) {
    return this.deviceConfigurationService.getSnapshotsFromRepository(
      deviceType,
      configurationType
    );
  }

  async updateConfigSnapshotOnEvent(event: IEvent, configurationType?) {
    const type = configurationType || this.configurationType;
    if (event && event.type === type) {
      this.configSnapshot = {
        time: event.time,
        name: event.text,
        deviceType: this.device.deviceType,
        configurationType: type
      };
      this.configSnapshot.binary = await this.deviceConfigurationService.getEventConfigurationBinary(
        event
      );
    }
  }

  async onRepositoryConfigSelected(config) {
    this.repositorySnapshot = {
      time: config.creationTime,
      name: config.name,
      binaryUrl: config.url,
      deviceType: config.deviceType,
      configurationType: config.configurationType
    };
    if (config.url) {
      this.repositorySnapshot.binary = await this.deviceConfigurationService.getConfigurationBinaryFile(
        config.url
      );
    }
  }

  ngOnDestroy() {
    this.eventsSubscription.unsubscribe();
  }
}
